<template>
  <div class="flex w-full flex-1 flex-col overflow-y-auto">
    <div class="w-full flex-1" :class="horizontalPadding">
      <table class="min-w-full flex-1" :class="bgClass">
        <!-- Table Header -->
        <TableHeader
          :headers="headers"
          :sort-key="sortKey"
          :sort-order="sortOrder"
          @fetch-sorted-data="handleSort"
          :bg-class="bgClass"
          :header-class="headerClass"
        />

        <!-- Table Body Slot -->
        <tbody>
          <slot name="table-body"></slot>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    headers: {
      type: Array,
      required: true,
    },
    sortKey: {
      type: String,
      required: false,
      default: "",
    },
    sortOrder: {
      type: String,
      required: false,
      default: "",
    },
    bgClass: {
      type: String,
      required: false,
      default: "bg-bg-neutral-default",
    },
    horizontalPadding: {
      type: String,
      required: false,
      default: "px-spacing-200 ipad:px-spacing-300",
    },
    headerClass: {
      type: String,
      default: "",
      required: false,
    },
  },
  components: {
    TableHeader: () => import("@/components/General/Table/TableHeader.vue"),
  },
  methods: {
    handleSort(options) {
      this.$emit("fetch-sorted-data", options);
    },
  },
};
</script>
